<template>
    <div  id="app" class="container mt-5">
        <div class="alert alert-primary" role="alert">
        <h2 class="d-flex text-center align-items-center text-dark">Agregar cliente</h2>
    </div>
    <!-- Seleccione la entidad y el nombre -->
        <b-card>
            <h3>Seleccione la Entidad</h3>
            <b-form-select v-model="selected" :options="entidad"></b-form-select>
            <h3 class="mt-2">Ingrese el nombre</h3>
            <input type="text" class="form-control my-3" v-model="nuevaPersona" v-on:keyup.enter="aggPersona" placeholder="Nombre">
            <button class="btn btn-primary" @click="aggPersona">Agregar</button>
        </b-card>
    <!-- Lista de personas -->
        <b-card class="mt-3">
          <h4>Clientes Personales</h4>
            <div class="mt-3" v-for="(item, index) of personas">
              <div class="alert alert-primary" role="alert">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <b>Nombre:</b> {{item.nombre}} 
                  </div>
                  <div>
                    <button class="btn btn-outline-primary btn-sm" @click="abrirModal(item._id)">Editar</button>
                    <button class="btn btn-danger btn-sm ml-2" @click="deleteClient(item._id)">x</button>
                  </div>
                </div>
              </div>
            </div>
          <h4>Clientes Empresariales</h4>
            <div class="mt-3" v-for="(item, index) of empresas">
              <div class="alert alert-primary" role="alert">
                <div class="d-flex justify-content-between align-items-center">
                  <div>
                    <b>Nombre:</b> {{item.nombre}} 
                  </div>
                  <div>
                    <button class="btn btn-outline-primary btn-sm" @click="abrirModal(item._id)">Editar</button>
                    <button class="btn btn-danger btn-sm ml-2" @click="deleteClient(item._id)">x</button>
                  </div>
                </div>
              </div>
            </div>
        </b-card>
          <b-modal
            v-model="openModal"
            @ok="updateClient"
            title="Ingresa el nuevo nombre"  
          >
            <b-input v-model="newName" placeholder="Nuevo nombre"></b-input>
          </b-modal>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name:'agregar-cliente',
    data(){
        return{
            personas: [],
            empresas: [],
            nuevaPersona: '',
            selected: null,
            openModal: false,
            newName: '',
            idCLient: '',
            entidad: [
                {value: null, text: 'Seleccione una entidad'},
                {value: 'persona', text: 'Persona'},
                {value: 'empresa', text: 'Empresa'},
            ],
        }
    },
    methods: {
        abrirModal(id) {
          this.idCLient = id;
          this.openModal= true;
        },
        aggPersona() {
        const This = this;
         if(!this.nuevaPersona.length || !this.selected.length){
          return This.$vToastify.info('Llena todos los datos para continuar')
        }
        if(this.selected == "persona"){
          axios
            .post('http://localhost:4012/api/createperson', {
            nombre: This.nuevaPersona,
            })
            .then((res) => {
                if(res.status==200){
                This.$vToastify.success('Persona agregada satisfactoriamente')
                This.findClients();
                This.selected = null;
                This.nuevaPersona= '';
                }else{
                This.$vToastify.error("Ha ocurrido un error")
                }})
            .catch((error) => {
                console.log(error)
                This.$vToastify.error(error)
            })
        }else{
          axios
            .post('http://localhost:4012/api/createcompany', {
            nombre: This.nuevaPersona,
            })
            .then((res) => {
                if(res.status==200){
                This.$vToastify.success('Compaia agregada satisfactoriamente')
                This.findClients();
                This.selected = null;
                This.nuevaPersona= '';
                }else{
                This.$vToastify.error("Ha ocurrido un error")
                }})
            .catch((error) => {
                console.log(error)
                This.$vToastify.error(error)
            })
        }
      },
      findClients() {
        const This= this;
        axios
          .get('http://localhost:4012/api/findclients')
          .then((res) => {
              if(res.status==200){
              This.personas = res.data.persona;
              This.empresas = res.data.empresa;
              This.$vToastify.success('Clientes obtenidos satisfactoriamente')
              }else{
              This.$vToastify.error("Ha ocurrido un error obteniendo los clientes")
              }})
          .catch((error) => {
              console.log(error)
              This.$vToastify.error(error)
          })
      },
      deleteClient(id) {
        const This= this;
        axios
          .delete(`http://localhost:4012/api/deleteclient/${id}`)
          .then((res) => {
              if(res.status==200){
              This.$vToastify.success('Clientes eliminado satisfactoriamente')
              This.findClients()
              }else{
              This.$vToastify.error("Ha ocurrido un error eliminado cliente")
              }})
          .catch((error) => {
              console.log(error)
              This.$vToastify.error(error)
          })
      },
      updateClient() {
        const This= this;
        const id = this.idCLient
        axios
          .put(`http://localhost:4012/api/updateclient/${id}`,{
            nombre: This.newName
          })
          .then((res) => {
              if(res.status==200){
              This.$vToastify.success('Clientes actualizado satisfactoriamente')
              This.findClients()
              This.newName = '';
              This.idCLient = '';
              }else{
              This.$vToastify.error("Ha ocurrido un error actualizado cliente")
              }})
          .catch((error) => {
              console.log(error)
              This.$vToastify.error(error)
          })
      }
    },
    mounted: function(){
      this.findClients();
    }
}
</script>